}
.home-nine .slick-dots li button {
  width: 6px;
  height: 6px;
  overflow: hidden;
  text-indent: 100px;
  border-radius: 50%;
  background: transparent;
  background: linear-gradient(
    90deg,
    #a146e8 -21.46%,
    #6c63d0 36.39%,
    #387db8 100%
  );
}
.home-nine .slick-dots li:before {
  content: "";
  width: 20px;
  height: 20px;
  left: 50%;
  top: 50%;
  opacity: 0;
  transition: 0.3s;
  position: absolute;
  border-radius: 50%;
  border: 2px solid #6c63d0;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.home-nine .slick-dots li.slick-active:before {
  opacity: 1;
}

.home-nine .slick-arrow {
  z-index: 1;
  position: relative;
  display: inline-block;
  width: 56px;
  height: 56px;
  background: #1b2429;
  line-height: 56px;
  border-radius: 50%;
  text-align: center;
  border: 1.5px solid rgba(255, 255, 255, 0.1);
}
.home-nine .slick-arrow:before {
  content: "";
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  opacity: 0;
  z-index: -1;
  transition: 0.5s;
  position: absolute;
  border-radius: 50%;
  background: linear-gradient(
    90deg,
    #a146e8 -21.46%,
    #6c63d0 36.39%,
    #387db8 100%
  );
}
.home-nine .slick-arrow:first-child {
  margin-right: 5px;
}
.home-nine .slick-arrow:focus:before,
.home-nine .slick-arrow:hover:before {
  opacity: 1;
}

